@import url("https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Roboto:wght@100;300;400;500;700;900&display=swap");
html {
  font-size: 16px;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

h1,
h2,
h3,
h4,
p {
  margin: 0;
  padding: 0;
}

body {
  overflow-x: hidden;
}

a {
  text-decoration: none;
}

ul {
  list-style-type: none;
}

.flex {
  display: flex;
}
.flex-jc-ctr {
  justify-content: center;
}
.flex-jc-sb {
  justify-content: space-between;
}
.flex-jc-sa {
  justify-content: space-around;
}
.flex-ai-ctr {
  align-items: center;
}
.flex-fd-col {
  flex-direction: column;
}

.grid {
  display: grid;
}
.grid-jc-ctr {
  justify-content: center;
}
.grid-pi-ctr {
  place-items: center;
}

.mobile-number {
  gap: 1rem;
}
.mobile-number h4 {
  font-family: "Bebas Neue", sans-serif;
  font-size: 3rem;
  color: hsl(0deg, 0%, 100%);
  font-weight: 400;
  text-align: center;
  width: 350px;
  flex: start;
}
.mobile-number #number-pad {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  width: 320px;
  gap: 1em;
  position: relative;
}
.mobile-number #number-pad #backspaceButton {
  height: 35px;
  width: 40px;
  position: absolute;
  top: 8px;
  right: 6px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  border-left: 1px solid hsl(0deg, 0%, 100%);
}
.mobile-number #number-pad #backspaceButton svg {
  height: 20px;
}
.mobile-number #number-pad #backspaceButton:hover svg {
  fill: hsl(0deg, 0%, 0%);
}
.mobile-number #number-pad #phone-number {
  width: 100%;
  padding: 12px;
  font-size: 20px;
  border-radius: 10px;
  outline: none;
  background: rgba(255, 255, 255, 0.375);
  box-shadow: 0 0.75rem 2rem 0 rgba(0, 0, 0, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.125);
  color: hsl(0deg, 0%, 100%);
}
.mobile-number #number-pad #phone-number:focus {
  background: rgba(255, 255, 255, 0.375);
  box-shadow: 0 0.75rem 2rem 0 rgba(0, 0, 0, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.125);
  outline: none;
}
.mobile-number #number-pad #buttons {
  width: 350px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  gap: 0.5em;
}
.mobile-number #number-pad #buttons .number {
  width: 100px;
  padding: 15px;
  font-size: 1.2rem;
  background: none;
  border-radius: 10px;
  background: rgba(255, 255, 255, 0.375);
  box-shadow: 0 0.75rem 2rem 0 rgba(0, 0, 0, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.125);
  color: hsl(0deg, 0%, 100%);
  cursor: pointer;
}
.mobile-number #number-pad #buttons .number:hover {
  background-color: hsl(0deg, 0%, 100%);
  box-shadow: 0 0.75rem 2rem 0 rgba(0, 0, 0, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.125);
  color: #4285F4;
}
.mobile-number #number-pad #buttons #clear-button,
.mobile-number #number-pad #buttons #confirm-button {
  width: 100px;
  padding: 15px;
  font-size: 1.2rem;
  border-radius: 10px;
  background: rgba(255, 255, 255, 0.375);
  box-shadow: 0 0.75rem 2rem 0 rgba(0, 0, 0, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.125);
  color: hsl(0deg, 0%, 100%);
  cursor: pointer;
}
.mobile-number #number-pad #buttons #clear-button:hover,
.mobile-number #number-pad #buttons #confirm-button:hover {
  background-color: hsl(0deg, 0%, 100%);
  box-shadow: 0 0.75rem 2rem 0 rgba(0, 0, 0, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.125);
  color: #4285F4;
}

body {
  background-color: hsl(0deg, 0%, 0%);
  color: hsl(0deg, 0%, 100%);
}

header {
  z-index: 2;
  padding: 0 10vw;
  height: 80px;
  position: relative;
}
header h2 {
  font-family: "Roboto", sans-serif;
  text-transform: uppercase;
}
header .brand-full-name {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: 0 auto;
  height: 50%;
  width: 350px;
  background-color: #4285F4;
  color: hsl(0deg, 0%, 0%);
  border-radius: 0 0 20px 20px;
}
header .brand-full-name p {
  font-family: "Roboto", sans-serif;
  font-size: 1.2rem;
}
header .time {
  text-align: end;
}
header .time div {
  font-family: "Roboto", sans-serif;
}
header .time div:nth-of-type(2) {
  color: #A7B2BD;
}

main {
  z-index: 2;
  width: 100vw;
  height: calc(100vh - 160px);
  font-family: "Bebas Neue", sans-serif;
  line-height: 0.9;
  letter-spacing: 1px;
  position: relative;
  overflow: hidden;
}
main h3 {
  font-size: 2.5rem;
  margin-bottom: 20px;
}
main h3:nth-of-type(2) {
  margin-top: -15px;
}
main h2 {
  font-size: 20rem;
  color: #FBBC04;
  clip-path: polygon(0 0, 100% 0, 100% 85%, 0% 85%);
  transform: translateY(-8px);
}
main h2:nth-of-type(2) {
  position: absolute;
  clip-path: polygon(0 0, 100% 0, 100% 85%, 0% 85%);
  color: hsl(0deg, 0%, 100%);
  transform: translateY(0);
}
main p {
  font-family: "Roboto", sans-serif;
  margin-top: -15px;
  text-transform: uppercase;
  color: #FBBC04;
}

footer {
  z-index: 2;
  padding: 0 10vw;
  height: 80px;
}
footer button {
  background-color: hsl(0deg, 0%, 100%);
  border: none;
  border-radius: 20px;
  padding: 0.5em 1.5em;
  font-family: "Roboto", sans-serif;
  font-size: 1rem;
  font-weight: 700;
  text-transform: uppercase;
  cursor: pointer;
}
footer button:hover, footer button:focus {
  background-color: #4285F4;
}
footer a {
  color: #A7B2BD;
  font-family: "Roboto", sans-serif;
}
footer a:hover, footer a:focus {
  color: hsl(0deg, 0%, 100%);
}

.aurora {
  width: 100vw;
  height: 100vh;
  position: absolute;
  background: #000;
  z-index: -99;
  opacity: 0.75;
  top: 0;
  left: 0;
}

.aurora:before,
.aurora:after {
  z-index: 1;
  content: "";
  display: block;
  position: absolute;
  width: 1px;
  height: 1px;
  border-radius: 100%;
  opacity: 0.18;
}

.aurora:before {
  box-shadow: 0 0 35vmax 35vmax #EA4335;
  animation: hue 5s 0s linear infinite, move1 10s 0s linear infinite;
}

.aurora:after {
  box-shadow: 0 0 35vmax 35vmax #4285F4;
  animation: hue 10s 0s linear infinite, move2 20s 0s linear infinite;
}

@-moz-keyframes hue {
  0% {
    filter: hue-rotate(0deg);
  }
  100% {
    filter: hue-rotate(360deg);
  }
}
@-webkit-keyframes hue {
  0% {
    filter: hue-rotate(0deg);
  }
  100% {
    filter: hue-rotate(360deg);
  }
}
@-o-keyframes hue {
  0% {
    filter: hue-rotate(0deg);
  }
  100% {
    filter: hue-rotate(360deg);
  }
}
@keyframes hue {
  0% {
    filter: hue-rotate(0deg);
  }
  100% {
    filter: hue-rotate(360deg);
  }
}
@-moz-keyframes move1 {
  0% {
    top: 0vh;
    left: 50vw;
  }
  25% {
    left: 0vw;
  }
  50% {
    top: 100vh;
  }
  75% {
    left: 100vw;
  }
  100% {
    top: 0vh;
    left: 50vw;
  }
}
